.banner {
    height: 60vh;
    width: 100%;
    position: relative;
    background-color: #34251F;
  }
  .bgImg {
    width: 100%;
    height: 100vh;
    object-fit: cover;
  }
  .home__content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 5%;
    display: flex;
    width: 100vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 10;
  }
  button.Home__button {
    padding: 15px 20px;
    background-color: tomato;
    border: none;
    cursor: pointer;
    color: #fff;
  }
  .homeHeading {
    text-align: center;
    border-bottom: 1px solid #917236;
    width: 20vmax;
    padding: 1vmax;
    font-size: 1.4vmax;
    margin: 5vmax auto;
    color:#917236
  }
  .container {
    display: flex;
    margin: 2vmax auto;
    width: 80vw;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 100%;
  }
  h1.discountPrice {
    font-size: 1vmax;
    text-decoration: line-through;
    padding-left: 10px;
  }
  .ProductCard {
    width: 14vmax;
    height: 22vmax;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: rgb(48, 48, 48);
    margin: 2vmax;
    transition: all 0.5s;
    padding-bottom: 0.5vmax;
  }
  img.ProductImg {
    width: 14vmax;
  }
  .ProductCard > div {
    margin: 0.5vmax;
    display: flex;
    justify-content: flex-start;
  }
  .ProductCard > div > span {
    margin: 0.5vmax;
    font: 300 0.7vmax "Roboto";
  }
  .productName {
    font-family: "Roboto";
    font-size: 1.2vmax;
    margin: 1vmax 0.5vmax;
    color: #000;
    margin-bottom: 0;
  }
  .ProductCard > span {
    margin: 0.5vmax;
    color: #917236;
    font-family: "Frankin Gothic Medium", "Arrial Narrow", Arial, sans-serif;
    font-size: 1vmax;
  }
  .ProductCard:hover {
    box-shadow: 0 0 5px #917236;
    transform: translateY(-1vmax);
  }
  @media screen and (max-width: 600px) {
    .ProductCard > p {
      font-size: 1.7vmax;
    }
    .ProductCard > div {
      margin: 0vmax;
      display: block;
    }
    .ProductCard > span {
      font-size: 1.5vmax;
    }
    .ProductCard > div > span {
      margin: 0 0.5vmax;
      font: 300 1vmax "Roboto";
    }
  }

  @media screen and (max-width: 600px) {
    .ProductCard > p {
      font-size: 1.7vmax;
    }
    .ProductCard > div {
      margin: 0vmax;
      display: block;
    }
    .ProductCard > span {
      font-size: 1.5vmax;
    }
    .ProductCard > div > span {
      margin: 0 0.5vmax;
      font: 300 1vmax "Roboto";
    }
  }
  