.LoginSignUpContainer {
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background-color: #fff;
  background-image: url('../../Assets/c.jpg');
  background-repeat: no-repeat;
  background-position: left;
  background-position-x: 10%;
  background-size: 90vh;
  position: fixed;
  top: 0%;
  left: 0;
}

.LoginSignUpBox {
  border: 2px solid #0000001a;
  padding: 10px;
  margin-right: 20%;
  width: 25vw;
  height: 70vh;
  overflow: hidden;
  box-shadow: 15px 15px 15px rgba(219, 213, 213, 0.2),
    -1px 2px 2px #917236;
  border-radius: 10px;
  background: none;
}

.login_signUp_toggle {
  display: flex;
  height: 3vmax;
}

.login_signUp_toggle>p {
  color: rgba(0, 0, 0, 0.678);
  font: 300 1vmax "Roboto";
  transition: all 0.5s;
  cursor: pointer;
  display: grid;
  place-items: center;
  width: 100%;
}

.login_signUp_toggle>p:hover {
  color: #997224;
}

.LoginSignUpBox>div>button {
  background-color: #917236;
  height: 3px;
  width: 50%;
  border: none;
  transition: all 0.5s;
}

.loginForm,
.signUpForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
}

.signUpForm {
  transform: translateY(-100%) translateX(-100vmax);
}

.loginForm>div,
.signUpForm>div {
  display: flex;
  width: 100%;
  align-items: center;
}

.loginForm>div>input,
.signUpForm>div>input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #917236;
  border-radius: 4px;
  font: 300 0.9vmax Poppins;
  outline: none;
}

.loginForm>div>svg,
.signUpForm>div>svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.623);
}

.loginForm>a {
  color: rgba(0, 0, 0, 0.651);
  text-decoration: none;
  align-self: flex-end;
  transition: all 0.5s;
  font: 500 0.8vmax "Gill Sans";
}

.loginForm>a:hover {
  color: #a77c27;
}

#registerImage>img {
  width: 3vmax;
  border-radius: 100%;
}

#registerImage>input {
  display: flex;
  padding: 0%;
  margin-left: 5px;
}

#registerImage>input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0%;
  font: 400 0.8vmax Poppins;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.623);
  background-color: rgb(255, 255, 255);
}

#registerImage>input::file-selector-button:hover {
  background-color: #a77c27a1;
}

.loginBtn,
.signUpBtn {
  border: none;
  background-color: #917236;
  color: white;
  font: 300 0.9vmax "Roboto";
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.loginBtn:hover,
.signUpBtn:hover {
  background-color: #a77c27;
}

.shiftToLeft {
  transform: translateX(-100%);
}

.shiftToNeutral {
  transform: translateX(0%);
}

.shiftToNeutralForm {
  transform: translateX(0%) translateY(-100%);
}

.shiftToRight {
  transform: translateX(100%);
}

@media screen and (max-width: 600px) {
  .LoginSignUpContainer {
    background-color: white;
  }

  .LoginSignUpBox {
    width: 100vw;
    height: 95vh;
  }

  .login_signUp_toggle {
    height: 5vmax;
  }

  .login_signUp_toggle>p {
    font: 300 1.5vmax "Roboto";
  }

  .loginForm,
  .signUpForm {
    padding: 5vmax;
  }

  .loginForm>div>input,
  .signUpForm>div>input {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax Poppins;
  }

  .loginForm>div>svg,
  .signUpForm>div>svg {
    font-size: 2.8vmax;
  }

  .loginForm>a {
    font: 500 1.8vmax "Gill Sans";
  }

  #registerImage>img {
    width: 8vmax;
    border-radius: 100%;
  }

  #registerImage>input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax Poppins;
  }

  .loginBtn,
  .signUpBtn {
    font: 300 1.9vmax "Roboto";
    padding: 1.8vmax;
  }
}